<template>
  <div class="shopcart">
    <div class="content" @click="toggleList">
      <!-- 购物车左侧 -->
      <div class="content-left">
        <div class="logo-wrapper">
          <div class="logo" :class="{'highlight':totalCount>0}">
            <i class="icon-shopping_cart" :class="{'highlight':totalCount>0}"></i>
          </div>
          <div class="num" v-show="totalCount>0">{{totalCount}}</div>
        </div>      
        <div class="price" :class="{'highlight':totalPrice>0}">￥{{totalPrice}}</div>
        <div class="desc">另需配送费￥{{deliveryPrice}}元</div>
      </div>
       <!-- 购物车右侧 -->
      <div class="content-right" @click.stop.prevent="pay">
        <div class="pay" :class="payClass">
          {{payDesc}}
        </div>
      </div>
    </div>
      <!-- 小球动画效果 -->
    <div class="ball-container">
      <div transition="drop" v-for="ball in balls" v-show="ball.show" class="ball">
        <div class="inner inner-hook"></div>
        <!-- 所有用js选择的样式名加个hook,仅仅为了选择 -->
      </div >
    </div>
      <!-- 购物车弹出层列表 -->
    <div class="shopcart-list" v-show="listShow" transition="fold">
      <div class="list-header">
        <h1 class="title">购物车</h1>
        <span class="empty" @click="empty">清空</span>
      </div>
      <div class="list-content" v-el:list-content>
        <!-- v-el是BSscroll需要的 -->
        <ul>
          <!-- 这里结构错误找了两个小时 好气哦-->
          <li class="food" v-for="food in selectFoods">
            <span class="name">{{food.name}}</span>
            <div class="price">
              <span>￥{{food.price*food.count}}</span>
            </div>
            <div class="cartcontrol-wrapper">
              <cartcontrol :food="food"></cartcontrol>
            </div>
          </li>
        </ul>
      </div>
    </div>
  </div>
  <!-- 购物车模糊遮罩 -->
  <div class="list-mask" v-show="listShow" transition="fade" @click="hideList"></div>
</template>
<script type="module"> 
  import BScroll from 'better-scroll';
  // 引入cartcontrol组件
  import cartcontrol from 'components/cartcontrol/cartcontrol'

  export default {
    props: {            // 父组件向子组件传递数据
      selectFoods: {
        type: Array,
        default() {
          return [{
            price: 0,
            count: 0
          }];
        }
      },
      deliveryPrice: {
        type: Number,
        default: 0
      },
      minPrice: {
        type: Number,
        default: 0
      }
    },
    data() {
      return {
        balls: [
          {
            show: false
          },
          {
            show: false
          },
          {
            show: false
          },
          {
            show: false
          },
          {
            show: false
          }
        ],
        // 已经下落小球变dropball
        dropBalls: [],
        fold: true
      };
    },
    computed: {
      totalPrice() {
        let total = 0;
        this.selectFoods.forEach((food) => {
          total += food.price * food.count;
        });
        return total;
      },
      totalCount() {
        let count = 0;
        this.selectFoods.forEach((food) => {
          count += food.count;
        });
        return count;
      },
      payDesc() {
        if(this.totalPrice === 0) {
          // es6语法
          return `￥${this.minPrice}元起送`;
        }else if(this.totalPrice<this.minPrice){
          let diff = this.minPrice - this.totalPrice;
          return `还差￥${diff}元起送`;
        }else{
          return '去结算';
        }
      },
      payClass() {
        if(this.totalPrice < this.minPrice){
          return 'not-enough'
        }else{
          return 'enough'
        }
      },
      listShow() {
        if(!this.totalCount){
          this.fold = true;
          return false;
        }
        let show = !this.fold;
        // 只有展开的时候才需要对betterscroll 做初始化
        if(show) {
          // 数据变了，dom变化并没有生效，bs依赖dom
          this.$nextTick(() => {
            if(!this.scroll) {
                this.scroll = new BScroll(this.$els.listContent,{
                click:true
              });
            } else {
              this.scroll.refresh();      // 如果存在，有个refresh接口，不用再次实例化
            }           
          });
        }
        return show;
      }
    },
    methods: {
      drop(el) {    //el就是cartcontrol的元素
        // console.log(el)         //  这里获得的是什么？？？？ 
        // 遍历balls
        for(let i=0;i<this.balls.length;i++) {
          let ball = this.balls[i];
          if(!ball.show) {
            ball.show = true;
            ball.el = el;         //把element保存
            this.dropBalls.push(ball);
            // alert(ball.show)
            return;
          }
        }
      },
      toggleList() {
        if(!this.totalCount) {
          return;
        }
        this.fold = !this.fold;   // 取反
      },
      // 清空购物车
      empty() {
        this.selectFoods.forEach((food) =>{
          food.count = 0;
        });
      },
      // 点击遮罩隐藏list
      hideList() {
        this.fold = true;
      },
      // 支付
      pay() {
        if(this.totalPrice<this.minPrice){
          return;
        }
        window.alert(`支付${this.totalPrice}`);
      }
    },
    transitions: {
      drop: {
        beforeEnter(el) {   // el当前执行transition的dom对象
          let count = this.balls.length;
          while (count--) {
            let ball =this.balls[count];
            if (ball.show) {
              let rect = ball.el.getBoundingClientRect();     // 调用浏览器接口,获得元素视口位置
              
              let x = rect.left - 32;
              // alert(x)
              let y = -(window.innerHeight - rect.top -22);
              // alert(y)
              el.style.display = '';  // 把元素置为空，显示出来
              // console.dir(el)
              el.style.webkitTransform = `translate3d(0,${y}px,0)`;
              el.style.transform = `translate3d(0,${y}px,0)`;
              let inner = el.getElementsByClassName('inner-hook')[0];
              // alert("纵向3D完成了")
              // alert(inner)
              // 横向的3d
              inner.style.webkitTransform = `translate3d(${x}px,0,0)`;
              inner.style.transform = `translate3d(${x}px,0,0)`;
            }
          } 
        },
        enter(el) {         // el 可以访问到22行
          // 触发浏览器重绘
          // eslint-disable no-unused-vars
          let rf = el.offsetHeight;
          this.$nextTick(() => {
            el.style.webkitTransform = 'translate3d(0,0,0)';
            el.style.transform = 'translate3d(0,0,0)';
            let inner = el.getElementsByClassName('inner-hook')[0];
            // 横向的3d
            inner.style.webkitTransform = 'translate3d(0,0,0)';
            inner.style.transform = 'translate3d(0,0,0)';
          });
        },
        afterEnter(el) {
          let ball = this.dropBalls.shift();
          if(ball) {          // 状态重置
            ball.show = false;
            el.style.display = 'none';
          }

        }
      }
    },
    components: {
      cartcontrol
    }
  };
</script>
<style lang="stylus" rel="stylesheet/stylus">

  @import "../../common/stylus/mixin"
  .shopcart
    position fixed
    left 0
    bottom 0
    z-index 50
    width 100%
    height 48px
    font-size 0
    .content
      display flex
      background #141d27
      .content-left
        /*自适应*/
        flex 1  
        .logo-wrapper
          display inline-block
          vartical-align top
          position relative
          top -10px
          margin 0 12px
          padding 6px
          width 56px
          height 56px
          /*宽度包括padding ie模型*/
          box-sizing border-box
          vertical-align top
          border-radius 50%
          background #141d27
          .logo
            width 100%
            height 100%
            border-radius 50%
            text-align center
            background #2b343c  
            &.highlight
              background rgb(0,160,220)         
            .icon-shopping_cart
              line-height 44px
              font-size 24px
              color #80858a
              &.highlight
                color #fff
        .num
          position absolute
          top 0
          right 0
          width 24px
          height 16px
          line-height 16px
          text-align center
          border-radius 16px
          font-size 9px
          font-weight 700
          color #fff
          background #f00
          b0x-shadow: 0 4px 8px 0 rgba(0, 0, 0, 0.4) 
        .price
          display inline-block
          vertical-align top
          margin-top 12px
          line-height 24px
          padding-right 12px
          box-sizing border-box
          border-right 1px solid rgba(255,255,255,0.1)
          font-size 16px
          font-weight 700
          color rgba(255,255,255,0.4)
          &.highlight
            color #fff
        .desc
          display inline-block
          vertical-align top
          margin 12px 0 0 12px
          line-height 24px
          color rgba(255,255,255,0.4) 
          font-size 10px
      .content-right
        flex 0 0 105px
        width 105px
        .pay
          height 48px
          line-height 48px
          text-align center
          font-size 12px
          color rgba(255,255,255,0.4) 
          font-weight 700
          background #2b333b
          &.not-enough
            background #2b333b
          &.enough
            background #00b43c
            color #fff
    .ball-container
      .ball
        position fixed
        left 32px
        bottom 22px
        z-index 2000
        &.drop-transition
          /*这里逗号写成.找了三个小时，好气好气啊*/
          transition all 0.4s cubic-bezier(0.49,-0.29,0.75,0.41)
          .inner
            width 16px
            height 16px
            border-radius 50%
            background rgb(0,160,220)
            /*x轴是线性的*/
            transition all 0.4s linear
    .shopcart-list
      position absolute
      top 0
      left 0 
      z-index -1
      width 100%
      &.fold-transition
        transition all 0.5s 
        /*这里是弹出特效*/
        transform translate3d(0, -100%, 0)
      &.fold-enter,&.fold-leave
        transform translate3d(0, 0, 0)
      .list-header
        height  40px
        line-height 40px
        padding 0 18px
        background #f3f5f7
        border-bottom 1px solid rgba(7,17,27,0.1)
        .title
          float left
          font-size 14px
          color rgb(7,17,27)
        .empty
          float right
          font-size 12px
          color rgb(0,160,220)
      .list-content
        padding 0 18px
        max-height 217px
        overflow hidden
        background #fff
        .food
          position relative
          padding 12px 0
          /*盒子模型*/
          box-sizing border-box
          border-1px(rgba(7,17,27,0.1))
          .name
            line-height 24px
            font-size 14px
            color rgb(7,17,27)
          .price
            position absolute
            right 90px
            bottom 12px
            line-height 24px
            font-size 14px
            font-weight 700
            color rgb(240,20,20)
          .cartcontrol-wrapper
            position absolute
            right 0
            bottom 6px
  .list-mask
    position fixed
    top 0 
    left 0
    width 100%
    height 100%
    z-index 40
    backdrop-filter blur(10px)
    &.fade-transition
      /*缓动*/
      transition all 0.5s
      opacity 1
      background rgba(7,17,27,0.6)
    &.fade-enter,&.fade-leave
      opacity 0
      background rgba(7,17,27,0)

</style>



